.workDetails__images{justify-self:right;width:auto;max-width:480px;min-height:calc(100vh - 125px);padding:105px 20px 20px;border-right:1px solid var(--darkGray);-webkit-box-sizing:content-box;box-sizing:content-box}.workDetails__images__image{width:100%;-webkit-box-shadow:0px 3px 10px 0px rgba(25,25,25,0.2);box-shadow:0px 3px 10px 0px rgba(25,25,25,0.2);cursor:pointer}.workDetails__images__image:not(:first-child){margin-top:30px}.mobileView .workDetails__images{grid-row:2 / 3;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-align:center;align-items:center;justify-self:center;-ms-flex-pack:distribute;justify-content:space-around;border-right:0;padding:0;max-width:none;min-height:initial}.mobileView .workDetails__images__image{-ms-flex:1 1 auto;flex:1 1 auto;margin:10px 0;max-width:500px}



