.filters__list{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:center;justify-content:center;max-width:1024px;margin:20px auto 10px}.filter{font-size:1.25rem;line-height:1;background-color:transparent;border:1px solid var(--darkGray);padding:10px;margin:10px;cursor:pointer;position:relative;-webkit-transition:background-color 0.35s ease-in-out;-o-transition:background-color 0.35s ease-in-out;transition:background-color 0.35s ease-in-out;overflow:hidden}.filter::-moz-selection{background:transparent}.filter::selection{background:transparent}.filter:hover:after{-webkit-transform:scaleX(1);-ms-transform:scaleX(1);transform:scaleX(1);-webkit-transform-origin:center left;-ms-transform-origin:center left;transform-origin:center left}.filter:after{content:"";width:100%;height:100%;position:absolute;left:0;top:0;-webkit-transform:scaleX(0);-ms-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:center right;-ms-transform-origin:center right;transform-origin:center right;-webkit-transition:-webkit-transform 0.35s ease-in-out;transition:-webkit-transform 0.35s ease-in-out;-o-transition:transform 0.35s ease-in-out;transition:transform 0.35s ease-in-out;transition:transform 0.35s ease-in-out, -webkit-transform 0.35s ease-in-out;z-index:-1}.filter--red:after{background-color:var(--redColor)}.filter--yellow:after{background-color:var(--yellowColor)}.filter--purple:after{background-color:var(--purpleColor)}.filter--green:after{background-color:var(--greenColor)}.filter--blue:after{background-color:var(--blueColor)}.filter--orange:after{background-color:var(--orangeColor)}.filter--active{color:var(--lightGray);background-color:var(--darkGray)}



